<template>
  <div class="confirm uk-text-center">
    <h1 v-if="isConfirmed.CWBB" class="uk-margin-large-top uk-text-danger">财务报表申报表审核结果</h1>
    <h1 v-if="!isConfirmed.CWBB" class="uk-margin-large-top uk-text-danger">数据审核无误，请点击“确认申报”进行申报！</h1>
    <div v-if="isConfirmed.CWBB" style="width: 900px; margin: 50px auto; font-size: 16px; line-height: 25px"
         class="uk-text-danger uk-text-left">
      数据已上报，请稍后查询反馈信息。
    </div>
    <div v-show="!isConfirmed.CWBB" style="margin-top: 20px">
      <button @click="confirm" class="uk-button uk-button-primary"> 确认申报</button>
    </div>
  </div>
</template>
<script>
  import mixins from '../../mixins/common'
  import { mapState } from 'vuex'

  export default {
    mixins: [mixins],
    data() {
      return {}
    },
    computed: {
      ...mapState(['isConfirmed'])
    },
    methods: {
      confirm() {
        this.$store.commit('setConfirm', { key: 'CWBB', value: true })
      }
    }
  }
</script>



